<template>
    <h1>Discord</h1>
    <input type="text" placeholder="Server Id:" v-model="ServerId" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Channel Id:" v-model="ChannelId" required/>
    <br />
    <br />
    <br />
    <input type="text" placeholder="Text:" v-model="text" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getDiscord">Confirm</button>
</template>

<script>    
export default {
    name: "DiscordREA",
    data() {
        return {
            ServerId: null,
            ChannelId: null,
            text: null,
            tmpREA: []
        }
    },
    methods: {
        getDiscord() {
            if (this.ChannelId.length >= 17 && this.ChannelId.length <= 19 &&
            this.ServerId.length >= 17 && this.ServerId.length <= 19) {
                let R = {
                    "serverId": this.ServerId,
                    "channelId": this.ChannelId,
                    "text": this.text
                }
                let REA = {
                    "content": JSON.stringify(R),
                    "id": "6337f6365955b62e4776c445",
                    "name": "Discord",
                    "description": "Send a message to a Discord channel"
                }
                if (localStorage.reaction) {
                    this.tmpREA = JSON.parse(localStorage.reaction)
                }
                this.tmpREA.push(REA)
                localStorage.reaction = JSON.stringify(this.tmpREA)
                this.$router.push({ name: 'Recap' });
            } else {
                alert('Discord IDs are between 17 and 19 numbers long.')
            }
        },
    }
}
</script>

